#cooperation {
    margin-bottom: 3.75rem;
}
.cooperation {
    &__container-fluid {
        background: linear-gradient(0deg, #363a4d, #363a4d), #f7f7f7;
        max-width: 113.75rem;
        width: 100%;
        padding: 0px;
        border-radius: 1.25rem;
        border: none;
    }
    // .cooperetion__inner

    &__inner {
        display: flex;
        flex-direction: column;
        padding: 5.8125rem 0;
    }

    // .cooperetion__header

    &__header {
        text-align: center;
        margin-bottom: 7.1875rem;
    }

    // .cooperetion__header-title

    &__header-title {
        position: relative;
        @include decktop-h2title(700, 2.25rem, 124.5%, uppercase);
        margin-bottom: 1.5rem;
        &::after {
            content: "";
            position: absolute;
            display: block;
            background-color: $after-background-color-lightgreen;
            width: 5.3125rem;
            height: 2px;
            left: calc(50% - 2.6563rem);
            bottom: -4.375rem;
        }
    }

    // .cooperetion__header-subtitle

    &__header-subtitle {
        font-weight: 300;
        font-size: 1.5rem;
        margin: 0;
    }

    // .cooperetion__content

    &__content {
        display: flex;
        flex-direction: column;
    }

    // .cooperetion__contnet-box

    &__contnet-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 3rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
}
.block-adaptive {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.cooperation {
    // .cooperation__info-list

    &__info-list {
        max-width: 33rem;
        padding: 0.3125rem;
        display: flex;
        flex-direction: column;
        margin: 0;
    }

    // .cooperation__info-item

    &__info-item {
        font-weight: 300;
        font-size: 1.0625rem;
        line-height: 156.5%;
        margin-bottom: 1.25rem;
        list-style: disc;
        list-style-position: inside;
        &:last-child {
            margin-bottom: 0px;
        }
    }

    // .cooperation__image
    &__image {
        padding: 5px;
    }
    &__image img {
        border-radius: 1.25rem;
        width: 100%;
        height: auto;
        filter: drop-shadow(5px 15px 40px rgba(11, 24, 97, 0.32));
    }

    // .cooperation__btn

    &__btn {
        display: flex;
        align-items: center;
        position: relative;
        @include btn(600, 1rem, #ffffff, 146%, 5.625rem, #e58d35);
        margin-top: 3rem;
        padding: 1.4375rem 5.875rem 1.4375rem 2.0625rem;
        &:hover {
            background-color: $button-background-hover-color;
        }
        &:active {
            color: $button-text-activ-color;
            transform: scale(1.02);
        }
    }
    &__btn-icon {
        position: absolute;
        right: 1.3125rem;
    }
}
