* {
    font-family: "ProximaNova";
    font-style: normal;
}
// standart global style
body {
    background-color: rgb(40, 43, 56);
    font-size: $global-font-size;
    font-weight: 400;
    line-height: 124.5%;
    color: $global-primary-color-text;
}
// container
[class*="__container"] {
    max-width: 73.75rem;
    margin: 0 auto;
    padding: 0 1.25rem;
}

[class*="__container-fluid"] {
    max-width: 120rem;
    margin: 0 auto;
    @include adaptiv-value("padding-left", 50, -230, 1);
    @include adaptiv-value("padding-right", 50, -230, 1);
}

header {
    position: sticky;
    top: 0;
    z-index: 50;
    background-color: inherit;
}
main {
    flex: 1 1 auto;
}
.wrapper {
    width: 100%;
    min-width: 20rem;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: hidden;
}
button {
    transition: all 0.3s linear;
}
