.footer {
    // .footer__inner

    &__inner {
        display: flex;
        padding: 40px 0 50px 0;
        border-top: 2px solid #e58d35;
    }

    // .footer__contact

    &__contact {
        display: flex;
        flex-direction: column;
        align-items: start;
        margin-right: 90px;
    }

    // .footer__logo

    &__logo {
        margin-bottom: 28px;
    }

    // .footer__contact-info-list

    &__contact-info-list {
        max-width: 263px;
        margin: 0 0 24px 0;
        padding: 0;
    }

    // .footer__contact-list-item

    &__contact-list-item {
        margin-bottom: 10px;
        &:last-child {
            margin: 0;
        }
    }

    // .footer__contact-list-link

    &__contact-list-link {
        font-weight: 400;
        font-size: 14px;
        line-height: 162.5%;
        color: $global-primary-color-text;
        transition: all 0.2s linear;
        &:hover {
            color: #89c249;
        }
    }

    // .footer__contact-social-list

    &__contact-social-list {
        max-width: 117px;
        display: flex;
        margin: 0;
        padding: 0;
    }

    // .footer__contact-social-item

    &__contact-social-item {
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
    }

    // .footer__contact-social-link

    &__contact-social-link {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 34px;
        height: 34px;
        border-radius: 100%;
        transition: all 0.1s linear;
        &:hover {
            transform: scale(1.2);
        }
    }
    // .footer__menu-block

    &__menu-block {
        display: flex;
        justify-content: space-between;
        width: 706px;
    }

    // .footer__content-block

    &__content-block {
        width: 208px;
        margin: 0 5px;
    }

    // .footer__contetn-block-title

    &__contetn-block-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 146%;
        margin: 0 0 15px 0;
    }

    // .footer__contetn-block-menu-list

    &__contetn-block-menu-list {
        margin: 0;
        padding: 0;
    }

    // .footer__menu-item

    &__menu-item {
        margin-bottom: 5px;
        &:last-child {
            margin-bottom: 0;
        }
    }

    // .footer__menu-link

    &__menu-link {
        position: relative;
        font-weight: 400;
        font-size: 16px;
        line-height: 160.5%;
        color: #85899c;
        &:hover {
            color: $footer-menu-link-color;
            &::after {
                content: "";
                position: absolute;
                display: block;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 1px;
                background-color: $footer-menu-link-after-color;
            }
        }
        &:active {
            color: #4065d6;
        }
    }
}

.instagram {
    background: linear-gradient(0deg, #e09b3d 0.46%, #c74c4d 22.72%, #c21975 60.76%, #7024c4 93.22%);
    &:active {
        box-shadow: 0 0 10px #c21975;
    }
}
.vcontact {
    background: #4065d6;
    &:active {
        box-shadow: 0 0 10px #4065d6;
    }
}
.telegram {
    background: #039be5;
    &:active {
        box-shadow: 0 0 10px #039be5;
    }
}
