#about-company {
    margin-bottom: 2.125rem;
}
.about {
    margin-bottom: 2.125rem;
    // .about__header-company

    &__header-company {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__header-company-content {
        max-width: 34.6875rem;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    &__header-image {
        max-width: 34.375rem;
    }
    &__img {
        max-width: 100%;
        height: auto;
    }

    // .about__about-title

    &__about-title {
        margin: 0 0 5.5rem 0;
    }

    // .about__title

    &__title {
        position: relative;
        text-align: center;
        @include decktop-h2title(700, 2.25rem, 124.5%, uppercase);
        margin: 0;
        &::after {
            content: "";
            position: absolute;
            display: block;
            background-color: $after-background-color-lightgreen;
            width: 5.3125rem;
            height: 2px;
            left: 0;
            bottom: -1.3125rem;
        }
    }

    // .about__about-subtitle

    &__about-subtitle {
        max-width: 34.6875rem;
    }

    // .about__subtitle

    &__subtitle {
        font-size: 1.125rem;
        line-height: 170%;
        text-align: start;
    }
}
