.navbar {
    height: 20px;
    padding: 30px 0;
    margin: 1.5625rem 0 2.5rem 0;
}

.navbar-brand {
    display: flex;
    flex: 0 0 auto;
    margin: 0;
    padding: 0;
}
.navbar-collapse {
    justify-content: space-between;
    opacity: 0.9;
}
.navbar-nav {
    display: flex;
    flex: 1 0 auto;
    justify-content: center;
    padding: 10px;
}
.nav-item {
    @include adaptiv-value("margin-right", 46, -125, 1);
    &:last-child {
        margin-right: 0;
    }
}
.nav-link--bs-white {
    color: #fff;
    position: relative;
    line-height: 146%;
    transition: 0.2s linear;
    &:hover {
        color: rgb(137, 194, 73);
        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 100%;
            height: 1px;
            border-radius: 1px;
            background-color: rgb(137, 194, 73);
            left: 0;
            bottom: 0;
        }
    }
    &:active {
        color: $button-text-activ-color;
    }
}
.nav-button {
    display: flex;
    flex: 0 0 auto;
    @include btn(600, 1rem, #ffffff, 146%, 5.625rem, #e58d35);
    padding: 0.8313rem 1.6875rem;
    &:hover {
        background-color: $button-background-hover-color;
    }
    &:active {
        color: $button-text-activ-color;
        transform: scale(1.02);
    }
}

.navbar-toggler {
    position: relative;
    width: 27.5px;
    height: 25px;

    border: none;
    transition: all 0.3s ease;
    &:focus {
        box-shadow: none;
    }
}
.burger-span {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    background: #fff;
    transition: 0.3s;
    &:nth-of-type(1) {
        transition: all 0.3s 0.3s, transform 0.3s 0s;
    }
    &:nth-of-type(2) {
        top: 50%;
        margin-top: -1px;
        left: 0;
        transition: all 0.3s 0.3s;
    }
    &:nth-of-type(3) {
        bottom: 0;
        top: auto;
        left: 0;
        transition: all 0.3s 0.3s, transform 0.3s 0s;
    }
}
[aria-expanded="true"] {
    .burger-span {
        transform: rotate(135deg);
        transition: all 0.5s 0.3s;
    }
    .burger-span:nth-of-type(1) {
        top: 50%;
        margin-top: -1px;
        transform: rotate(45deg);
        transition: all 0.3s, transform 0.3s 0.3s;
    }
    .burger-span:nth-of-type(2) {
        opacity: 0;
        transition: all 0.3s, opacity 0.3s 0s;
    }
    .burger-span:nth-of-type(3) {
        bottom: 50%;
        margin-bottom: -1px;
        transform: rotate(-45deg);
        transition: all 0.3s, transform 0.3s 0.3s;
    }
}
