#first-screen {
    margin-bottom: 39px;
}
.slick-list {
    overflow: hidden;
    border-radius: 20px;
}
.slick-track {
    display: flex;
}

.firstcreen-slider {
    width: 100%;
    position: relative;
}

.first-screen {
    &__container-fluid {
        max-width: 120rem;
        padding: 0;
    }
    // .first-screen__slider-item

    &__slider-item {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
}
.slider-item {
    // .slider-item__container

    &__container {
        display: flex;
        align-items: center;
        min-height: 700px;
        padding: 75px 20px;
        margin: 0 auto;
    }

    &__item-content {
        min-width: 100%;
    }

    // .slider-item__title

    &__title {
        font-weight: 700;
        font-size: 56px;
        max-width: 463px;
        text-transform: uppercase;
        margin-bottom: 8px;
        line-height: 126%;
    }

    // .slider-item__subtitle

    &__subtitle {
        font-weight: 300;
        font-size: 20px;
        line-height: 135.5%;
        max-width: 345px;
        margin-bottom: 37px;
    }

    // .slider-item__button

    &__btn {
        display: flex;
        align-items: center;
        position: relative;
        @include btn(600, 1rem, #ffffff, 146%, 5.625rem, #e58d35);
        margin-top: 3rem;
        padding: 23px 94px 23px 33px;
        &:hover {
            background-color: $button-background-hover-color;
        }
        &:active {
            color: $button-text-activ-color;
            transform: scale(1.02);
        }
    }
    &__btn-icon {
        position: absolute;
        right: 1.3125rem;
    }
}
.slick-arrow {
    position: absolute;
    z-index: 2;
    top: 50%;
    font-size: 0px;
    color: #ffffff;
}
.slick-prev {
    left: 0;
}
.slick-next {
    right: 0;
}

.slick-dots {
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 2.4375rem;
    right: calc(50% - 7.5rem);
    margin: 0;
    padding: 0;
}
.slick-dots li {
    width: 8px;
    height: 8px;
    border-radius: 5.625rem;
    opacity: 0.9;
    font-size: 0;
    border: 2px solid $global-primary-color-text;
    margin: 0 1.625rem;
}
.slick-active {
    background-color: $global-primary-color-text;
}
