@media screen and (max-width: 991px) {
    //header
    .navbar {
        height: 4.375rem;
        padding: 0.9375rem 0;
        margin: 0;
    }
    .navbar-brand {
        img {
            width: 80%;
        }
    }
    .navbar-collapse {
        display: flex;
        flex-direction: column;
        background-color: $burger-menu-background;
        margin: 0.9375rem 0 0;
    }
    .navbar-nav {
        text-align: center;
        margin-top: 3.4375rem;
        padding: 0;
    }
    .nav-item {
        margin: 0 0 48px 0;
        &:last-child {
            margin: 0 0 3rem 0;
        }
    }
    .nav-link--bs-white {
        font-size: 1.5rem;
        line-height: 222%;
        &:hover {
            &::after {
                content: "";
                position: absolute;
                display: none;
                width: 100%;
                height: 1px;
                border-radius: 1px;
                background-color: $after-background-color-lightgreen;
                left: 0;
                bottom: 0;
            }
        }
    }
    .nav-button {
        font-size: $global-font-size;
        padding: 1.4375rem 5.3125rem;
        margin-bottom: 3.75rem;
    }

    // first screen slider
    #first-screen {
        margin-bottom: 55px;
    }
    .slider-item {
        &__container {
            justify-content: center;
        }
        &__content {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        &__title {
            text-align: center;
            font-size: 2.25rem;
        }
        &__subtitle {
            text-align: center;
            font-size: 1rem;
        }
    }

    //about-company
    #about-company {
        margin-bottom: 2.125rem;
    }
    .about {
        &__header-company {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
        }
        &__header-company-content {
            align-items: center;
            margin-bottom: 1.5625rem;
        }

        &__about-title {
            @include mobile-h2title(700, 1.5rem, 124.5%, uppercase);
            margin: 0 0 3.9375rem 0;
        }
        &__title {
            &::after {
                left: calc(50% - 2.6563rem);
            }
        }
        &__subtitle {
            text-align: justify;
            font-size: $global-font-size;
            line-height: 149%;
        }
    }

    //cooperation
    .cooperation {
        &__container-fluid {
            padding: 0;
        }
        &__header {
            margin-bottom: 4.375rem;
        }
        &__header-title {
            @include mobile-h2title(700, 1.5rem, 124.5%, uppercase);
        }
        &__contnet-box {
            flex-direction: column-reverse;
            margin-bottom: 1.875rem;
        }
        &__info-list {
            margin-bottom: 1.8125rem;
        }
        &__info-item {
            font-size: 1rem;
        }
        &__image {
            margin-bottom: 1.875rem;
        }
        &__btn {
            padding: 1.4375rem 11.625rem 1.4375rem 2.0625rem;
        }
    }
    .box-revers {
        flex-direction: column;
    }

    //footer
    .footer {
        &__inner {
            display: flex;
            justify-content: center;
        }
        &__contact {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin: 0;
        }
        &__menu-block {
            display: none;
        }
    }
}
