// Font-family
$font-family-primary: "Proxima Nova";

// Color global text
$global-primary-color-text: rgb(255, 255, 255);

// Font global
$global-font-size: 1rem;
$global-line-height: 124.5%;

// footer text
$footer-menu-link-color: rgb(133, 137, 156);
$footer-menu-link-after-color: rgb(133, 137, 156);
$footer-border-top-color: rgb(229, 141, 53);

// burger menu
$burger-btn-color: rgb(255, 255, 255);
$burger-menu-background: rgb(54, 58, 77);

// button
$button-background-color: rgb(229, 141, 53);
$button-text-color: rgb(255, 255, 255);
$button-background-hover-color: rgb(137, 194, 73);
$button-text-hover-color: rgb(0, 0, 0);
$button-text-activ-color: rgb(0, 25, 150);

// after === title
$after-background-color-orange: rgb(229, 141, 53);
$after-background-color-lightgreen: rgb(137, 194, 73);

// icon background
$icon-background: rgb(137, 194, 73);
